
export function getAppletName() {
    var url = window.location.href;

    // Regex deseni
    var regexPattern = /\/applet\/([^\/]+)/;

    // Regex elemesi
    var matches = url.match(regexPattern);

    // Eer eleme varsa, list parametresini al
    if (matches && matches.length > 1) {
        return matches[1];

    } else {
        return;
    }

}


